<template>
	<div class="content-preview">
		<div class="content-preview__title mb-2" v-if="title">
			{{ title }}
		</div>

		<div class="content-preview__block">
			<div class="content-preview__params">
				<div class="content-preview__param mb-2" v-for="(param, index) in content" :key="index">
					<div class="content-preview__param-label color-gray small pr-1">{{ param.label }}:</div>
					<div class="content-preview__param-value color-beige" v-html="param.value"></div>
				</div>
			</div>

			<div class="content-preview__poster" v-if="poster">
				<div class="color-gray small mb-3">Постер:</div>
				<img :src="poster" :alt="title">
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "ContentPreview",

		props: {
			content: {
				type: Array,
				required: true,
			},
			title: {
				type: String,
			},
			poster: {
				type: String,
			},
		},
	}
</script>

<style lang="less" scoped>
	.content-preview {
		margin-top: 1.5rem;
		padding-top: 1.5rem;
		border-top: dotted 1px white;

		&__title {
			font-size: 18px;
			color: #ddb089;
			margin-bottom: 10px;
			font-weight: bold;
		}

		&__block {
			display: flex;
		}

		&__params {
			flex: 2;
		}

		&__poster {
			flex: 1;
			padding-left: 1.5rem;

			img {
				max-width: 100%;
			}
		}

		&__param {
			display: flex;
			line-height: 26px;
		}
	}
</style>


